nav{
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(45deg,rgba(156,14,156),midnightblue);
    margin: 0;
    padding: 0;
}

.liste{
    list-style-type: none;
    background: linear-gradient(45deg,rgba(156,14,156),midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 22px;
}
.liste a{
 text-decoration: none;
 color: #f1f1f1;
}
.items{
    margin:0 15px;
    text-transform: uppercase;
    color: #f1f1f1;
    cursor: pointer;
    /* background: #f1f1f1; */
}

.btn{
    display: none;
    position: absolute;
    right: 10px;
    top: 7px;
    padding: 5px;
    color: #000;
    font-size: 18px;
}

@media screen and (max-width:500px){
    .liste{
        flex-direction: column;
        height: auto;
    }
    .items:nth-child(1){
        border-top: 1px solid #fff;
        margin-top: 50px;
    }
    .items{
        height: 70px;
        width: 100%;
        border-bottom: 1px solid #fff;
        line-height: 70px;
        text-align: center;
    }
    .btn{
        display: block;
    }
}